import axios from 'axios'
import { Dialog, Toast } from 'vant';
import { BASE_URL } from '../config'

/**
 * http
 */
class Http {
  constructor(BASE_URL, options = {}) {
    if (!BASE_URL) throw Error('need first param BASE_URL ')
    let { headers = {}, auth = false, ...others } = options
    this.auth = auth
    this.agent = axios.create({
      baseURL: BASE_URL,
      headers: {
        // 'Cache-Control': 'max-age=50',
        Accept: 'application/json',
        'Content-Type': 'application/json',
        ...headers
      },
      ...others
    })
    this.agentFile = axios.create({
      headers: {
        'Content-Type': 'multipart/form-data',
      }
    })
    this.initInterceptors()
  }
  // 
  initInterceptors() {
    // 
    this.agent.interceptors.request.use((config) => {
      if (this.auth) {
        if(sessionStorage.getItem("token") != null){
          config.headers.token = sessionStorage.getItem("token")
        }
      }
      return config
      // token  config.data
    })

    // 
    this.agent.interceptors.response.use((res) => {
      return res.data;
    })
  }
  get(url, params = {}) {
    //  Promise pending
    // getBanner().then(res => console.log(res)).catch(err => console.log(err))  then  catch
    //  await getBanner() 
    return new Promise((resolve, reject) => {
      this.agent.get(url, { params })
      .then(res => {
        if (res.code === 200) {
          resolve(res)
        }
      })
      .catch((err) => {
        if(err.response != undefined){
          if(err.response.data.code == 400){
            Dialog({ message: err.response.data.msg });
          }else if(err.response.status == 403){
            Dialog({ message: err.response.data.msg, beforeClose: ()=>{
              sessionStorage.removeItem("token");
              location.href = "/login";
            }});
          }
        }else {
          Dialog({ message: '' });
        }
        reject(err);
      })
    })
  }
  post(url, params = {}, config = {}) {
    //  get 
    return new Promise((resolve, reject) => {
      this.agent.post(url, params, config)
      .then((res) => {
        if (res.code === 200) {
          resolve(res);
        }
      })
      .catch((err) => {
        if(err.response != undefined){
          if(err.response.data.code == 400){
            Dialog({ message: err.response.data.msg });
          }else if(err.response.status == 403){
            Dialog({ message: err.response.data.msg, beforeClose: ()=>{
              sessionStorage.removeItem("token");
              location.href = "/login";
            }});
          }
        }else {
          Dialog({ message: '' });
        }
        reject(err);
      })
    })
  }
  delete(url, params = {}) {
    return new Promise((resolve, reject) => {
      this.agent.delete(url, { data: params })
      .then(res => {
        if (res.code === 200) {
          resolve(res)
        }
      })
      .catch((err) => {
        if(err.response != undefined){
          if(err.response.data.code == 400){
            Dialog({ message: err.response.data.msg });
          }else if(err.response.status == 403){
            Dialog({ message: err.response.data.msg, beforeClose: ()=>{
              sessionStorage.removeItem("token");
              location.href = "/login";
            }});
          }
        }else {
          Dialog({ message: '' });
        }
        reject(err);
      })
    })
  }
  put(url, params = {}) {
    return new Promise((resolve, reject) => {
      this.agent.put(url, params)
      .then(res => {
        if (res.code === 200) {
          resolve(res)
        }
      })
      .catch((err) => {
        if(err.response != undefined){
          if(err.response.data.code == 400){
            Dialog({ message: err.response.data.msg });
          }else if(err.response.status == 403){
            Dialog({ message: err.response.data.msg, beforeClose: ()=>{
              sessionStorage.removeItem("token");
              location.href = "/login";
            }});
          }
        }else {
          Dialog({ message: '' });
        }
        reject(err);
      })
    })
  }
  postFileUpload(url, params = {}, config = {}) {
    //  get 
    return new Promise((resolve, reject) => {
      this.agentFile.post(url, params, config)
      .then((res) => {
        resolve(res);
      })
      .catch((err) => {
        Toast.clear();
        Dialog({ message: '' });
      })
    })
  }
}

